const ColorPalette = {
  // From Option 1: Standard Blue
  BASE: {
    INDICES: "#93C5FD", // (Tailwind Blue-300)
    DATA: "#E0F2FE",    // (Tailwind Sky-100)
  },

  // From Option 3: Dark Teal
  HEADERS: {
    // Dark, rich teal header (assumes light/white text)
    INDICES: "#0F766E", // (Tailwind Teal-700)
    DATA: "#0F766E",    // (Tailwind Teal-700)
  },

  // From Option 1: Standard Blue
  HIGHLIGHT: {
    INDICES: "#60A5FA", // (Tailwind Blue-400)
    DATA: "#BFDBFE",    // (Tailwind Blue-200)
  },

  // From Option 1: Standard Red
  ALERT: {
    ERROR: "#FCA5A5",   // (Tailwind Red-300)
  },
  
  NO_FILL: null,
};
/**
 * Processes encrypted shares from the server into a structured format.
 * Groups shares into chunks of 3 and formats them for Excel output.
 * @param encryptedShares - Flat array of encrypted share strings from the server
 * @param FormattedIndices - Original index values from the sheet after the join operation. 
 * @param username - Data owner's username
 * @returns 2D array formatted for Excel worksheet
 * @throws Error if share count is invalid
 */
function formatEncryptedShares(encryptedShares: string[], FormattedIndices: any[][]): string[][] {
    const SHARES_PER_ROW = 3;
    const results: string[][] = [];

    if (encryptedShares.length % SHARES_PER_ROW !== 0) {
        throw new Error(`Invalid server response: Expected share count to be a multiple of ${SHARES_PER_ROW}.`);
    }

    const concatenatedShares = []
    for (let i = 0; i < encryptedShares.length; i += SHARES_PER_ROW) {

        const shareGroup = encryptedShares.slice(i, i + SHARES_PER_ROW);
        concatenatedShares.push(shareGroup.join("|"));
    }
    let tempFormattedIndices=FormattedIndices.map((row) => row.join("|")).flat()
    
    console.log("tempFormattedIndices:", tempFormattedIndices);
    for (let i =0; i < tempFormattedIndices.length; i+=1) {
        const row = [tempFormattedIndices[i], concatenatedShares[i]];
        console.log(row)
        results.push(row);
    }


    return results;
}


function colorExcelRange(context: Excel.RequestContext, address: string, color: string | null): void {
  // Only attempt to format if an address has been set
    if (!address) { return;}

    try {
        const addressParts = address.split("!");
        const relativeAddress = addressParts.pop();
        const range = context.workbook.worksheets.getActiveWorksheet().getRange(relativeAddress);

        console.log(`Coloring for range: ${relativeAddress} with color: ${color}`);
        console.log("color value: ", color);
        
        if (color === ColorPalette.NO_FILL || color == null) {
            console.log("NO FILL condition met");
            console.log(`Clearing fill color for range: ${relativeAddress}`);
            range.format.fill.clear();
        } else {
        range.format.fill.color = color;
        }
    } catch (error) {
        // This can fail if the sheet was deleted or renamed.
        console.warn(`Could not color for range ${address}: ${error.message}`);
    }
}

/**
 * Gets the background fill color of a cell.
 * @param context The Excel RequestContext.
 * @param address The cell address (e.g., "A1" or "Sheet1!A1").
 * @returns A promise that resolves to the color (e.g., "#FFFFFF") or null if the cell has no fill.
 */
async function getCellColor(context: Excel.RequestContext, address: string): Promise<string | null> {
    try {
        // 1. Get the range from the address.
        // This handles "A1" (active sheet) or "Sheet1!A1" (specific sheet).
        const range = context.workbook.worksheets.getActiveWorksheet().getRange(address);

        // 2. Get the top-left cell, in case a multi-cell range was given.
        const cell = range.getCell(0, 0);

        // 3. Load the specific property we want to read from Excel.
        cell.load("format/fill/color");

        // 4. Synchronize with Excel to fetch the loaded property.
        await context.sync();

        // 5. Return the color. This will be a hex string (e.g., "#FF0000").
        return cell.format.fill.color;

    } catch (error) {
        // 6. Handle errors. A common error is "ItemNotFound" which
        //    Excel throws if you try to read the color of a "No Fill" cell.
        if (error instanceof OfficeExtension.Error && error.code === "ItemNotFound") {
            // This means the fill property doesn't exist, i.e., "No Fill"
            console.log(`Cell ${address} has no fill.`);
            return null; // Return null to represent "No Fill"
        }

        // Handle other potential errors (e.g., invalid address)
        console.warn(`Could not get color for cell ${address}: ${error.message}`);
        throw error; // Re-throw other errors
    }
}



/**
 * Converts a string to Base64-encoded UTF-8 bytes.
 * @param value - The string to encode
 * @returns Base64-encoded string
 */
function encodeToBase64(value: string): any {
    const utf8Bytes = new TextEncoder().encode(value);
    const binaryString = String.fromCharCode(...Array.from(utf8Bytes));
    //btoa takes binary strings and encodes them to Base64 ASCII
    return btoa(binaryString);
}


export {formatEncryptedShares, encodeToBase64, colorExcelRange,getCellColor ,ColorPalette};