<template>
        <div
        v-if="statusMessage"
        class="notification is-light"
        :class="{
            'is-success': statusMessage.includes('success'),
            'is-danger': statusMessage.includes('Error'),
            'is-info': !statusMessage.includes('success') && !statusMessage.includes('Error'),
        }"
        >
        {{ statusMessage }}
        </div>
</template>

<script setup>
const props = defineProps({
    statusMessage: {
        type: String,
        required: false,
        default: "",
    },
});


</script>