/**
 * A helper function to generate a single key of a specific type for a user.
 * @param user The username.
 * @param keyType The type of key to generate ('ECC', 'AES', or 'SHA').
 */
async function generateKey(user: string, keyType: 'ECC' | 'AES' | 'SHA'): Promise<any> {
    const fullUrl = 'https://gapi.zellwwf.com/rpc/GenerateKeys';
    const payloadObject = {
        payload: {
            key_type: keyType,
            user: user
        }
    };

    const options = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(payloadObject)
    };

    console.log(`Generating ${keyType} key for user: ${user}`);
    const response = await fetch(fullUrl, options);
    
    if (!response.ok) {
        const errorBody = await response.json();
        throw new Error(`Failed to generate ${keyType} key: ${JSON.stringify(errorBody)}`);
    }

    return await response.json();
}

/**
 * Orchestrates the generation of all required keys (ECC, AES, SHA) for a user.
 * Runs the API calls in parallel for better performance.
 * @param user The username for whom to generate keys.
 */
export async function generateAllKeysForUser(user: string, onSuccess: Function, onFailure: Function): Promise<void> {
    const keyTypes: ('ECC' | 'AES' | 'SHA')[] = ['ECC', 'AES', 'SHA'];
    
    // Create an array of promises, one for each key generation call
    const keyGenerationPromises = keyTypes.map(type => {
        generateKey(user, type).then(onSuccess(type)).catch(onFailure(type));
    });

    await Promise.all(keyGenerationPromises);
    console.log(`All keys for user: ${user} where attempted :D see previous logs for what succeeded`);
}


/**
 * Sends the clear data and indices to the server to be converted into secret shares.
 * @param payload The object containing the user, data, and indices.
 * @returns The server's response, which should contain the encrypted data.
 */
export async function writeSensitiveData(payload: { user: string, data: number[], indices: number[] }): Promise<any> {
    const fullUrl = 'https://gapi.zellwwf.com/rpc/WriteSensitiveData';
    
    // The payload for the body is the entire object, including the "payload" key
    const bodyPayload = {
        payload: payload
    };
    const options = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(bodyPayload)
    };

    console.log("Sending sensitive data to the server...");
    const response = await fetch(fullUrl, options);
    console.log("Received response from server.");
    console.log("Response status:", response.status);

    if (!response.ok) {
        const errorBody = await response.json();
        throw new Error(`API Error from WriteSensitiveData: ${JSON.stringify(errorBody)}`);
    }

    console.log("Server responded successfully.");
    return await response.json();
}