import { defineStore } from 'pinia';
import { ref } from 'vue';
import { generateAllKeysForUser, writeSensitiveData } from "../../shared/apis";

export const useUserStore = defineStore('user', () => {
  // Read initial value from localStorage
  const currentUser = ref<string | null>(localStorage.getItem('currentUser-nera'));

  function login(username: string) {
    currentUser.value = username;
    // Call API with credentials, and API returns a valid Token and user_id. we use user_id as our user, and token when we need to auth.
    localStorage.setItem('currentUser-nera', username);
    generateAllKeysForUser(username, onKeySuccess, onKeyFailure);
  }

  function logout() {
    currentUser.value = null;
    localStorage.removeItem('currentUser-nera');
    localStorage.removeItem('nera-sha-keys-generated-at');
    localStorage.removeItem('nera-aes-keys-generated-at');
    localStorage.removeItem('nera-ecc-keys-generated-at');
  }

  function onKeySuccess(key: string) {
    const validKeys = ['SHA', 'ECC', 'AES'];

    console.log(`- Key ${key} is success`)
    // throw error if key is not one of ('SHA', 'ECC', 'AES')
    if (!validKeys.includes(key)) {
      throw new Error(`Invalid key: ${key}. Must be one of 'SHA', 'ECC', or 'AES'.`);
    }

    // then transform the string to lowercase and inject it in the setItem string.
    const lowerCaseKey = key.toLowerCase();
    localStorage.setItem(`nera-${lowerCaseKey}-keys-generated-at`, new Date().toISOString());
  }

  function onKeyFailure(key: string) {
    const validKeys = ['SHA', 'ECC', 'AES'];

    console.log(`- Key ${key} is fail`)

    // throw error if key is not one of ('SHA', 'ECC', 'AES')
    if (!validKeys.includes(key)) {
      throw new Error(`Invalid key: ${key}. Must be one of 'SHA', 'ECC', or 'AES'.`);
    }
  }

  return { currentUser, login, logout };
});