import { SecretRange } from "./secret_range";

export class Headers {
    indicesHeaders: string[];
    dataHeaders: string[];
    concatenatedHeaders: Promise<string[]>;
    indicesRanges: SecretRange
    dataRanges: SecretRange



    constructor(indicesRanges: SecretRange, dataRanges: SecretRange) {
        if (indicesRanges == null || dataRanges == null) {
            throw new Error("IndicesRanges and DataRanges must be provided");
        }
        if (indicesRanges.inputRange == "" || dataRanges.inputRange == "") {
            throw new Error("Input ranges for IndicesRanges and DataRanges must be set");
        }
        if (indicesRanges.endRow - indicesRanges.startRow != 0 || dataRanges.endRow - dataRanges.startRow != 0) {
            throw new Error("Only single row ranges are supported for headers");
        }
        
        this.indicesRanges = indicesRanges;
        this.dataRanges = dataRanges;
    }

   async fillHeadersValues(indicesRange: SecretRange, dataRanges: SecretRange): Promise<void> {
       await Excel.run(async (context) => {
            const sheet = context.workbook.worksheets.getActiveWorksheet();
            const excelIndicesRange = sheet.getRange(this.indicesRanges.inputRange);
            const excelDataRange = sheet.getRange(this.dataRanges.inputRange);
            excelIndicesRange.load("values");
            excelDataRange.load("values");
            await context.sync();
            this.indicesHeaders = excelIndicesRange.values.flat() as string[];
            this.dataHeaders = excelDataRange.values.flat() as string[];
        });
    }

   async getConcatenatedHeaders(): Promise<string[]> {
       await this.fillHeadersValues(this.indicesRanges, this.dataRanges);
        const concatenatedIndicesHeaders = this.indicesHeaders.join(" | ");
        const concatenatedDataHeaders = this.dataHeaders.join(" | ");
        return [concatenatedIndicesHeaders, concatenatedDataHeaders];
    }


}