<template>
  <div class="container mt-4">
    <div class="columns is-centered">
      <div class="column is-two-thirds-tablet is-half-desktop">
        <!-- Page Title -->
        <h2 class="title has-text-centered">Welcome Back</h2>
        <p class="subtitle has-text-centered is-size-6">
          Please sign in to your account to continue.
        </p>

        <!-- Login Card -->
        <div class="card">
          <div class="card-content">
            <!-- Username Field -->
            <div class="field">
              <label for="username-input" class="label">Username</label>
              <div class="control">
                <input
                  type="text"
                  id="username-input"
                  class="input"
                  placeholder="e.g., user@example.com"
                  v-model="username"
                  @keyup.enter="handleLogin"
                />
              </div>
            </div>

            <!-- Password Field -->
            <div class="field">
              <label for="password-input" class="label">Password</label>
              <div class="control">
                <input
                  type="password"
                  id="password-input"
                  class="input"
                  placeholder="Your password"
                  @keyup.enter="handleLogin"
                />
              </div>
            </div>

            <!-- Login Button -->
            <div class="field mt-5">
              <p class="control">
                <button
                  id="login-button"
                  class="button is-primary is-fullwidth"
                  @click="handleLogin"
                  :disabled="!username"
                >
                  Login
                </button>
              </p>
            </div>

            <div class="field mt-5">
              <p class="control">
                <a class="button is-ghost" href="#"> Forgot your password? </a>
                <a class="button is-ghost" href="#"> Create an account? </a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { useUserStore } from "../stores/user_store";

// Get the Pinia store
const userStore = useUserStore();

// Local state for the input field
const username = ref("");

const handleLogin = () => {
  if (userStore.currentUser) {
    console.log("User Already Logged in ?!?!");
  }
  if (username.value) {
    console.log("loogging in with ..", username.value);
    userStore.login(username.value);
  }
};
</script>

<style scoped>
.login-panel {
  padding: 10px;
}
.input-group {
  display: flex;
  margin-top: 5px;
}
.input-group input {
  flex-grow: 1;
}
.step {
  margin-top: 20px;
}
</style>
