<template>
    <div class="field">
        <label class="label">{{ props.label }}</label>
        <div class="field has-addons">
            <p class="control is-expanded">
                <input class="input" type="text" placeholder="e.g., D2:F50" v-model="range" />
            </p>
            <p class="control">
                <button class="button is-info" @click="props.rangeSelectorHandler">{{ props.buttonText }}</button>
            </p>
        </div>
    </div>
</template>

<script setup>
import { computed } from "vue";
const props = defineProps({
    label: {
        type: String,
        required: true,
    },
    buttonText: {
        type: String,
        required: true,
    },
    rangeSelectorHandler: {
        type: Function,
        required: true,
    },
    modelValue: {
        type: String,
        required: true,
    },

});
const emit = defineEmits(['update:modelValue'])
const range = computed({
    get() {
        return props.modelValue;
    },
    set(value) {
        emit('update:modelValue', value);

    }
});

</script>
