<template>
  <main class="nera-app container">

    <Login v-if="!userStore.currentUser" />

    <div v-else>
      <div class="tabs">
        <ul>
          <li :class="{ 'is-active': currentView === 'tools' }"><a @click="goToSecret()">Protect Data</a></li>
          <li :class="{ 'is-active': currentView === 'help' }"><a @click="goToHelp()">Help</a></li>
        </ul>
        <div class="is-left">
          <button class="button is-small" @click="userStore.logout()">Sign Out</button>
        </div>
      </div>

      <SecretShare v-if="currentView === 'tools'" />
      <ComputeHelp v-if="currentView === 'help'" />

    </div>
  </main>
</template>

<script setup>
import { ref } from 'vue';
import { useUserStore } from './stores/user_store';

import Login from './components/Login.vue';
import SecretShare from './components/SecretShare.vue';
import ComputeHelp from './components/ComputeHelp.vue';

const goToSecret = () => {
  currentView.value = 'tools';
}

const goToHelp = () => {
  currentView.value = 'help';
}

const userStore = useUserStore();
const currentView = ref('tools'); // State in view, use ref on something and thats it.
</script>