<template>
  <tr v-if="pair" @mouseenter="highlightRange" @mouseleave="clearHighlight">
    <td>
      <p class="is-bold is-primary">Pair: {{ props.index }}</p>
    </td>
    <td>
      <p class="">Indices: {{ pair.indices.inputRange }}</p>
    </td>
    <td>
      <p class="">Data: {{ pair.data.inputRange }}</p>
    </td>
    <td>
      <button class="delete is-medium" @click="deletePair"></button>
    </td>
  </tr>
</template>

<script setup>
import { ref } from "vue";
import SecretPair from "../models/secret_pair";
import { useSecretRangeStore } from "../stores/secret_ranges_store";
import { colorExcelRange, ColorPalette } from "../../shared/utils";
const secretRangeStore = useSecretRangeStore();
const pairLabel = ref("");
const props = defineProps({
  pair: {
    type: SecretPair,
    required: true,
  },
  setStatus: {
    type: Function,
    required: true,
  },
  index: {
    type: Number,
    required: false,
    default: 0,
  }
});


const updatePairColor = async (indicesColor, dataColor) => {
  try {
    await Excel.run(async (context) => {
      const indicesRange = props.pair.indices.inputRange
      const dataRange = props.pair.data.inputRange
      colorExcelRange(context, indicesRange, indicesColor);
      colorExcelRange(context, dataRange, dataColor);
      await context.sync();
    });
  } catch (error) {
    console.error("Failed to update pair color:", error);
  }
};


const deletePair = async () => {
  try {
    await updatePairColor(ColorPalette.NO_FILL, ColorPalette.NO_FILL);
    console.log("Deleting pair with id:", props.pair.id);
    secretRangeStore.deletePair(props.pair.id);
    props.setStatus(`Deleted pair: ${props.pair.label}`, 3000);
  }
  catch (error) {
    console.error("Failed to delete pair:", error);
    props.setStatus(`Error deleting pair: ${error.message}`, 5000);
  };
};

const highlightRange = async () => {
  try {
    await updatePairColor(ColorPalette.HIGHLIGHT.INDICES, ColorPalette.HIGHLIGHT.DATA);
  } catch (error) {
    console.error("Failed to highlight pair:", error);
  }
};

const clearHighlight = async () => {
  try {
    await updatePairColor(ColorPalette.BASE.INDICES, ColorPalette.BASE.DATA);

  } catch (error) {
    console.error("Failed to clear highlight for pair:", error);
  }
};



</script>
