class SecretRange {
    static _ranges: SecretRange[] = [];
    startRow: number;
    startCol: number;
    endRow: number;
    endCol: number;
    inputRange: string;
    range_type: string; // Either data or index.

    constructor(blockRange: string) {
        this.rangeToIndices(blockRange);
        this.inputRange = blockRange;
        // only after it passes the test (valid range), we add it to the list
        // otherwise u have to throw error
        // for (const existingRange of SecretRange._ranges) {
            // if (SecretRange.isOverlapping(this, existingRange)) {
            //     throw new Error(`Range ${blockRange} overlaps with existing range ${existingRange.inputRange}`);
            // }
            // if (SecretRange.isContained(this, existingRange)) {
            //     throw new Error(`Range ${blockRange} is contained within existing range ${existingRange.inputRange}`);
            // }

        // }
        SecretRange._ranges.push(this);
        console.log("secret range created:", this.inputRange);
    }

    //blockRange format: "A1:C30" --> startRow=1, startCol=1, endRow=30, endCol=3
    rangeToIndices(blockRange: String): void {
        const fullRange = blockRange.split("!");
        const parts = fullRange[1].split(":");
        const startCell = parts[0];
        let endCell: string;
        if (parts.length < 2) {
            endCell = startCell;
        }
        else {
            endCell = parts[1];
        }
        this.startRow = parseInt(startCell.replace(/[^0-9]+/g, ''));  // keep only digits   
        this.startCol = this.columnLetterToNumber(startCell.replace(/[^A-Z]/g, ''));
        this.endRow = parseInt(endCell.replace(/[^0-9]/g, ''));
        this.endCol = this.columnLetterToNumber(endCell.replace(/[^A-Z]/g, ''));

    }

    columnLetterToNumber(colLetter: String): number {  
        let colNumber = 0;
        for (let i = 0; i < colLetter.length; i++) {
            colNumber = colNumber * 26 + (colLetter.charCodeAt(i) - 'A'.charCodeAt(0) + 1);
        }
        return colNumber;
    }

   static isOverlapping(range1: SecretRange,range2: SecretRange): boolean {
        if (range1.endRow < range2.startRow || range1.startRow > range2.endRow) {
            return false;
       }
       if (range1.endCol < range2.startCol || range1.startCol > range2.endCol) {
           return false;
         }
       return true;
    
    }
    
    //a1:c8 -> inner block is b2:c8
   
    static isContained(range1: SecretRange,range2:SecretRange): boolean {
        if (range2.startCol > range1.startCol)              return false;
        if (range2.endCol < range1.endCol)                  return false;
        if (range2.startRow > range1.startRow)              return false;
        if (range2.endRow < range1.endRow)                  return false;
        return true;
    }


    // unionRanges(ranges: SecretRange[]): SecretRange {
    //     let minStartRow = this.startRow;
    //     let maxEndRow = this.endRow;
    //     for (const range of ranges) {
    //         if (range.startRow < minStartRow) {
    //             minStartRow = range.startRow;
    //         }
    //         if (range.endRow > maxEndRow) {
    //             maxEndRow = range.endRow;
    //         }
    //     }
    //     return new SecretRange(minStartRow, this.startCol, maxEndRow, this.endCol);
    // }
}

