<template>
  <div class="container mt-4">
    <div class="columns is-centered">
      <div class="column is-two-thirds-tablet is-half-desktop">
        <h2 class="title has-text-centered">Secure Data Preparation</h2>
        <p class="subtitle has-text-centered is-size-6">
          Select your data ranges to prepare them for secure sharing.
        </p>

        <div class="card">
          <div class="card-content">
            <!-- Secret Pair Form Component -->
            <SecretPairForm />
          </div>
        </div>

        <!-- Status Message Component -->
        <div id="status-feedback" class="status mt-4">
          <StatusMessage :statusMessage="statusMessage" />
        </div>
        <!-- Table of Protected Ranges -->
        <div class="card p-4">
          <h4 class="title is-5 mb-3 has-text-centered">
            Ready to Be Protected
          </h4>
          <div class="box">
            <div class="container is-centered mt-5">
              <div class="mx-3" v-if="secretRangeStore.pairs.length > 0">
                <table class="table">
                  <tbody v-for="(pair, index) in secretRangeStore.pairs" :key="pair.id">
                    <ProtectedRange :pair="pair" :index="index" :setStatus="setStatus" />
                  </tbody>
                </table>
              </div>

              <div class="mx-3" v-else>
                <div class="notification">You haven't created any secure ranges yet.</div>
              </div>
            </div>

            <!-- Secure Data Button -->
            <div class="mt-5">
              <button :disabled="secretRangeStore.pairs.length == 0" @click="handleSecureData"
                class="button is-primary is-fullwidth">
                Create Protected Sheet
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { useSecretRangeStore } from "../stores/secret_ranges_store";
import ProtectedRange from "../components/ProtectedRange.vue";
import StatusMessage from "../components/StatusMessage.vue";
import SecretPairForm from "./SecretPairForm.vue";
import { useStatus } from "../composables/useStatus";


const secretRangeStore = useSecretRangeStore();
const { statusMessage, setStatus } = useStatus();



const handleSecureData = async () => {
  setStatus("Securing data...", 5000);
  try {
    await Excel.run(async (context) => {
      const pairs = secretRangeStore.pairs;
      for (const pair of pairs) {
        await pair.protectPair();
      }
      setStatus("Data secured successfully!", 5000);
      secretRangeStore.createEncryptedDataSheet(context);
    });
  } catch (error) {
    setStatus("Error securing data." + error.message, 5000);
    console.error(error);
  }
};
</script>

<style scoped></style>
