import { defineStore } from 'pinia';
import { ref } from 'vue';
import { SecretRange } from '../models/secret_range';
import SecretPair from '../models/secret_pair';
import { Headers } from '../models/headers';
import { formatEncryptedShares } from '../../shared/utils'
import { on } from 'process';

export const useSecretRangeStore = defineStore('secretRanges', () => {
  const nextId = ref(0);
  const ranges = ref([]); // Array of SecretRange objects
  const pairs = ref([]);  // Array of SecretPair objects
  const headers = ref<Headers | null>(null); // Headers object


  function addRange(cellRect: string) {
    console.log('store is creating a new range', cellRect);
    const x = new SecretRange(cellRect);
    ranges.value.push(x);
    return x;
  }

  function addHeaders(indicesRange: SecretRange, dataRange: SecretRange) {
    headers.value = new Headers(indicesRange, dataRange);
    return headers.value;
  }


  function clearHeaders() {
    headers.value = null;
  }
  function checkPairFollowHeadersRange(pair: SecretPair, headers: Headers): void {

    //check pair indices range is within headers indices range
    if (pair.indices.startCol != headers.indicesRanges.startCol ||
      pair.indices.endCol != headers.indicesRanges.endCol) {
      throw new Error("Indices range of this pair is out of headers indices range");
    }
    //check pair data range is within headers data range
    if (pair.data.startCol != headers.dataRanges.startCol ||
      pair.data.endCol != headers.dataRanges.endCol) {
      throw new Error("Data range of this pair is out of headers data range");
    }

  }

  function addPair(id: number, label: string, indices: SecretRange, data: SecretRange, onSuccess: Function, onFailure: Function) {

    console.log('store is creating a new pair', label);
    try {
      const x = new SecretPair(id, label, data, indices);
      checkPairFollowHeadersRange(x, headers.value);
      pairs.value.push(x);
      nextId.value += 1;
      onSuccess(x)
      return x;
    }
    catch (e) {
      onFailure(e, indices, data);
    }
  }

  function deletePair(id: number) {
    // this is used because splice modifies the array in place and shifts indices after deletion
    const index = pairs.value.findIndex(pair => pair.id === id);
    if (index === -1) {
      throw new Error(`Pair with id ${id} not found`);
    }
    const deletedPair = pairs.value.splice(index, 1);

    return deletedPair;
  }

  async function createEncryptedDataSheet(context: Excel.RequestContext) {
    // Get a list of worksheet names and make sure u create a new name
    // const resultSheet = context.workbook.worksheets.add("Protected0");


    const workSheets = context.workbook.worksheets;
    const countResult = workSheets.getCount();
    await context.sync();
    const countValue = countResult.value;
    const resultSheet = context.workbook.worksheets.add("Protected Data Sheet " + countValue);
    const concatenatedHeaders = await headers.value.getConcatenatedHeaders();

    const headerRowValues = [
      [
        concatenatedHeaders[0],           // A1
        "Data Owner Protected Shares",    // B1
        "Data Owner"                      // C1
      ]
    ];
    // Set values for cells A1:C1 in one call
    resultSheet.getRange("A1:C1").values = headerRowValues;
    resultSheet.getCell(1, 2).values = pairs.value[0].data_owner;


    //reset row index to start writing data below headers
    let i = 1
    let j = 0
    for (const pair of pairs.value) {
      console.log("Creating encrypted data for pair:", pairs.value);
      if (!pair._encrypted_shares || !Array.isArray(pair._encrypted_shares)) {
        throw new Error("pair.encrypted_shares is not array");
      }
      const formattedResults = formatEncryptedShares(pair._encrypted_shares, pair._indices_ary);
      const destinationRange = resultSheet.getRangeByIndexes(
        i,
        j,
        formattedResults.length,
        formattedResults[0].length
      );
      i += formattedResults.length;
      destinationRange.values = formattedResults;
      resultSheet.getUsedRange().format.autofitColumns();
    }

    await context.sync();

  }

  return { ranges, pairs, headers, nextId, addRange, addHeaders, clearHeaders, addPair, deletePair, createEncryptedDataSheet };
}
);