<template>

    <div v-if="secretRangeStore.headers === null">
        <label class="label">Step 1: Select Headers (one-time setup)</label>
        <div class="box">
            <SelectRange label="Indices Headers Range" buttonText="Set Indices Headers"
                :rangeSelectorHandler="selectIndicesHeaders" v-model="indicesHeadersRange" />

            <SelectRange label="Data Headers Range" buttonText="Set Data Headers"
                :rangeSelectorHandler="selectDataHeaders" v-model="dataHeadersRange" class="mt-3" />

            <div class="field mt-5">
                <p class="control">
                    <button class="button is-primary is-fullwidth" :disabled="!indicesHeadersRange || !dataHeadersRange"
                        @click="saveHeaders">Save Headers</button>
                </p>
            </div>

        </div>
    </div>
    <div v-else>
        <label class="label">Step 2: Select the Data with its range</label>
        <div class="box">
            <!-- select indices ranges -->
            <SelectRange label="Select Indices Range" buttonText="Set Indices" :rangeSelectorHandler="selectIndices"
                v-model="indicesRange" />

            <!-- select data ranges -->
            <SelectRange label="Select Data Range" buttonText="Set Data" :rangeSelectorHandler="selectData"
                v-model="dataRange" />

            <div class="is-flex is-justify-content-space-between is-align-items-center">
                <div>
                    <p class="has-text-weight-semibold">Headers Configured</p>
                    <p class="is-size-7 has-text-grey">
                        Header ranges are highlighted in your spreadsheet
                    </p>
                </div>
                <button class="button is-small is-light" @click="editHeaders">
                    <span>Edit</span>
                </button>
            </div>
            <div class="field mt-5">
                <p class="control">
                    <button class="button is-primary is-fullwidth" :disabled="!indicesRange || !dataRange"
                        @click="saveRange">Create Secure Range</button>
                </p>
            </div>
        </div>
    </div>

</template>

<script setup>
import { ref } from "vue";
import SelectRange from "../components/SelectRange.vue";
import { SecretRange } from "../models/secret_range";
import { useSecretRangeStore } from "../stores/secret_ranges_store";
import { useStatus } from "../composables/useStatus";
import { colorExcelRange, getCellColor, ColorPalette } from "../../shared/utils";

const secretRangeStore = useSecretRangeStore();
const { setStatus } = useStatus();

const indicesRange = ref("");
const dataRange = ref("");
const indicesHeadersRange = ref("");
const dataHeadersRange = ref("");

const currentIndices = ref({});
const currentData = ref({});
const currentIndicesHeaders = ref({});
const currentDataHeaders = ref({});

const createRangeSelector = (selectedRange, rangeObject, color, successMessage, errorMessage, rangeHandler) => {
    return async () => {
        try {
            await Excel.run(async (context) => {
                const range = context.workbook.getSelectedRange();
                range.load("address");
                await context.sync();

                const x = rangeHandler(range.address);

                colorExcelRange(context, range.address, color);
                setStatus(successMessage, 3000);

                selectedRange.value = range.address;
                rangeObject.value = x;
            });
        } catch (error) {
            setStatus(errorMessage, 4000);
            console.error(error);
        }
    };
};

const selectIndices = createRangeSelector(
    indicesRange,
    currentIndices,
    ColorPalette.BASE.INDICES,
    "Selected Indices",
    "Error selecting indices.",
    (address) => secretRangeStore.addRange(address)
);

const selectData = createRangeSelector(
    dataRange,
    currentData,
    ColorPalette.BASE.DATA,
    "Selected Data",
    "Error selecting data.",
    (address) => secretRangeStore.addRange(address)
);



// TODO: Pick another color scheme for headers
const selectIndicesHeaders = createRangeSelector(
    indicesHeadersRange,
    currentIndicesHeaders,
    ColorPalette.HEADERS.INDICES,
    "Selected Indices Headers",
    "Error selecting indices headers.",
    (address) => new SecretRange(address)
);


const selectDataHeaders = createRangeSelector(
    dataHeadersRange,
    currentDataHeaders,
    ColorPalette.HEADERS.DATA,
    "Selected Data Headers",
    "Error selecting data headers.",
    (address) => new SecretRange(address)
);

const saveRange = () => {
    console.log("Current ranges in store:", secretRangeStore.ranges);
    console.log("Current pairs in store:", secretRangeStore.pairs);
    // @todo add validators such that if any of the params are empty show an error (in the bottom panel)
    const id = secretRangeStore.nextId;
    const label = "Pair" + String(secretRangeStore.nextId);

    const onSuccess = (pair) => {
        // display message
        setStatus("Secure range created successfully!", 3000);
        indicesRange.value = "";
        dataRange.value = "";
    };

    const onFailure = async (error, indices, data) => {
        Excel.run(async (context) => {
            // remove colored ranges
            console.log("indices input range in remove the color" + indices.inputRange);
            // TODO: reset color to it's original form not just no fill
            colorExcelRange(context, indices.inputRange, ColorPalette.NO_FILL);
            colorExcelRange(context, data.inputRange, ColorPalette.NO_FILL);
            await context.sync();
            setStatus("Error creating secure range: " + error.message, 5000);
        });
    };

    secretRangeStore.addPair(
        id,
        label,
        currentIndices.value,
        currentData.value,
        onSuccess,
        onFailure
    );

};
const saveHeaders = () => {
    try {
        secretRangeStore.addHeaders(currentIndicesHeaders.value, currentDataHeaders.value);
        setStatus("Headers saved successfully!", 3000);
    }
    catch (error) {
        setStatus("Error selecting headers: " + error.message, 4000);
        console.error("Error saving headers:", error);
    }

};

const editHeaders = () => {
    secretRangeStore.clearHeaders();
    indicesHeadersRange.value = "";
    dataHeadersRange.value = "";
}


</script>
