import { SecretRange } from "./secret_range";
import { encodeToBase64 } from "../../shared/utils"
import { writeSensitiveData } from "../../shared/apis";
import {useUserStore} from "../stores/user_store"
export default class SecretPair {
    id:number;
    label: String;
    indices: SecretRange;
    data: SecretRange;
    data_owner: string;

    // Excel Values Holders
    _data_ary: number[];
    _indices_ary: number[];
    
    // Protected State
    _encrypted_shares: Array<any>;
    is_protected: false;

    constructor(id: number, label: string, data: SecretRange, indices: SecretRange) {
         
        if (data.startCol !== data.endCol) {
            console.log("data obj:",data);
            throw new Error(`Data range must be a single column ${data.startCol}, but got startCol=${data.endCol}`);
        }

        if (data.startRow !== indices.startRow) {
            throw new Error('Indices and Data are not aligned from the top, please reselect them');
        }
        if (data.endRow !== indices.endRow) {
            throw new Error('Indices and Data are not aligned from the bottom, please reselect them');
        }
        this.id = id;
        this.label = label;
        this.data = data;
        this.indices = indices;
        this.data_owner = useUserStore().currentUser as string;
    }

    async protectPair() {
        await this.prepareDataArrays();
    
        const requestPayload = {
            user: this.data_owner,
            data: this._data_ary,
            indices: this._indices_ary.map((row) => encodeToBase64(row.join("|"))).flat()
        };
        console.log("requestPayload for writeSensitiveData:", requestPayload);
        const resp = await writeSensitiveData(requestPayload)
        console.log("pretty print of writeSensitiveData response:" , JSON.stringify(resp, null, 2));
        this._encrypted_shares = resp.encryptedShares;


    }

    private async prepareDataArrays() {
        await Excel.run(async (context) => {
            const activeSheet = context.workbook.worksheets.getActiveWorksheet();
            const indicesRange = activeSheet.getRange(this.indices.inputRange);
            const dataRange = activeSheet.getRange(this.data.inputRange);
            
            indicesRange.load(["values", "rowCount"]);
            dataRange.load(["values", "rowCount", "columnCount"]);

            await context.sync();
            this._indices_ary = indicesRange.values;
            // hard rule any data array should be single column
            this._data_ary = dataRange.values.flat();

        });
    }
}