___CSS_LOADER_EXPORT___.push([module.id, "\n.login-panel[data-v-62003ee9] {\r\n  padding: 10px;\n}\n.input-group[data-v-62003ee9] {\r\n  display: flex;\r\n  margin-top: 5px;\n}\n.input-group input[data-v-62003ee9] {\r\n  flex-grow: 1;\n}\n.step[data-v-62003ee9] {\r\n  margin-top: 20px;\n}\r\n", "",{"version":3,"sources":["webpack://./src/app/components/Login.vue"],"names":[],"mappings":";AA2FA;EACE,aAAa;AACf;AACA;EACE,aAAa;EACb,eAAe;AACjB;AACA;EACE,YAAY;AACd;AACA;EACE,gBAAgB;AAClB","sourcesContent":["<template>\r\n  <div class=\"container mt-4\">\r\n    <div class=\"columns is-centered\">\r\n      <div class=\"column is-two-thirds-tablet is-half-desktop\">\r\n        <!-- Page Title -->\r\n        <h2 class=\"title has-text-centered\">Welcome Back</h2>\r\n        <p class=\"subtitle has-text-centered is-size-6\">\r\n          Please sign in to your account to continue.\r\n        </p>\r\n\r\n        <!-- Login Card -->\r\n        <div class=\"card\">\r\n          <div class=\"card-content\">\r\n            <!-- Username Field -->\r\n            <div class=\"field\">\r\n              <label for=\"username-input\" class=\"label\">Username</label>\r\n              <div class=\"control\">\r\n                <input\r\n                  type=\"text\"\r\n                  id=\"username-input\"\r\n                  class=\"input\"\r\n                  placeholder=\"e.g., user@example.com\"\r\n                  v-model=\"username\"\r\n                  @keyup.enter=\"handleLogin\"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <!-- Password Field -->\r\n            <div class=\"field\">\r\n              <label for=\"password-input\" class=\"label\">Password</label>\r\n              <div class=\"control\">\r\n                <input\r\n                  type=\"password\"\r\n                  id=\"password-input\"\r\n                  class=\"input\"\r\n                  placeholder=\"Your password\"\r\n                  @keyup.enter=\"handleLogin\"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <!-- Login Button -->\r\n            <div class=\"field mt-5\">\r\n              <p class=\"control\">\r\n                <button\r\n                  id=\"login-button\"\r\n                  class=\"button is-primary is-fullwidth\"\r\n                  @click=\"handleLogin\"\r\n                  :disabled=\"!username\"\r\n                >\r\n                  Login\r\n                </button>\r\n              </p>\r\n            </div>\r\n\r\n            <div class=\"field mt-5\">\r\n              <p class=\"control\">\r\n                <a class=\"button is-ghost\" href=\"#\"> Forgot your password? </a>\r\n                <a class=\"button is-ghost\" href=\"#\"> Create an account? </a>\r\n              </p>\r\n            </div>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script setup>\r\nimport { ref } from \"vue\";\r\nimport { useUserStore } from \"../stores/user_store\";\r\n\r\n// Get the Pinia store\r\nconst userStore = useUserStore();\r\n\r\n// Local state for the input field\r\nconst username = ref(\"\");\r\n\r\nconst handleLogin = () => {\r\n  if (userStore.currentUser) {\r\n    console.log(\"User Already Logged in ?!?!\");\r\n  }\r\n  if (username.value) {\r\n    console.log(\"loogging in with ..\", username.value);\r\n    userStore.login(username.value);\r\n  }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.login-panel {\r\n  padding: 10px;\r\n}\r\n.input-group {\r\n  display: flex;\r\n  margin-top: 5px;\r\n}\r\n.input-group input {\r\n  flex-grow: 1;\r\n}\r\n.step {\r\n  margin-top: 20px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
